import { useEffect, useState } from 'react';
import {
  ProductApi,
  ProductSaveApi,
  ProductDelApi,
} from '../../utils/adminApi';
import {
  Table,
  Button,
  Modal,
  Upload,
  message,
  Form,
	Input,
  InputNumber,
  Select,
} from 'antd';
import { PlusOutlined } from '@ant-design/icons';
const { Column } = Table;
const { Option } = Select;
const {TextArea} = Input

const Product = () => {
  const [tableData, setTableData] = useState();
  const [visible, setVisible] = useState(false);
  const [imageUrl, setImageUrl] = useState(null);
  const [form] = Form.useForm();

  useEffect(() => {
    ProductApi().then((res) => {
      setTableData(
        res.data.map((item) => ({
          ...item,
          _id: item._id.slice(0, 7),
					id: item._id
        }))
      );
    });
  }, []);

  const edit = (record) => {
		form.setFieldsValue(record)
		setImageUrl(record.imageUrl)
		setVisible(true)
	};
  const del = (record) => {
		ProductDelApi(record.id).then((res) => {
			const newTableData = tableData.filter((item) => item.id !== record.id)
			setTableData(newTableData)
		})
	};

  const onChange = (info) => {
    if (info.file.status === 'done') {
      message.success('文件上传成功');
      setImageUrl(info.file.response);
    } else if (info.file.status === 'error') {
      message.error('文件上传失败');
    }
  };

  const onFinish = (value) => {
    ProductSaveApi({...value, imageUrl }).then((res) => {
      message.success('保存成功');
      onCancel();
			const swapData = tableData.filter((item) => item.id !== res.data._id)
      setTableData([
        ...swapData,
        {
					...res.data,
          _id: res.data._id.slice(0, 7),
          id: res.data._id,
        },
      ]);
    });
  };

  const onCancel = () => {
    setVisible(false);
    setImageUrl(null);
		form.resetFields()
  };

  return (
    <>
      <Button
        type="primary"
        className="bg-blue-600"
        onClick={() => setVisible(true)}
      >
        添加产品
      </Button>
      <Modal
        title="添加产品"
        centered
        visible={visible}
        onCancel={onCancel}
        width={1000}
        keyboard={false}
        maskClosable={false}
        footer={[]}
      >
        <Form
          labelCol={{ span: 4 }}
          form={form}
          name="control-hooks"
          onFinish={onFinish}
        >
          <Form.Item name="id" style={{display: 'none'}}>
            <Input/>
          </Form.Item>
          <Form.Item name="name" label="产品名称" rules={[{ required: true }]}>
            <Input/>
          </Form.Item>
          <Form.Item name="issue" label="期数" rules={[{ required: true }]}>
            <InputNumber min={1}/>
          </Form.Item>
          <Form.Item name="price" label="单价" rules={[{ required: true }]}>
            <InputNumber min={1}/>
          </Form.Item>
          <Form.Item name="stock" label="库存" rules={[{ required: true }]}>
            <InputNumber min={1}/>
          </Form.Item>
          <Form.Item name="period" label="运行周期" rules={[{ required: true }]}>
            <InputNumber min={1}/>
          </Form.Item>
          <Form.Item name="income" label="收益" rules={[{ required: true }]}>
            <InputNumber min={0} stringMode/>
          </Form.Item>
          <Form.Item name="limit" label="限购" rules={[{ required: true }]}>
            <InputNumber min={1}/>
          </Form.Item>
          <Form.Item name="status" label="状态" rules={[{ required: true }]}>
            <Select
              placeholder="请选择产品是上架还是下架"
              allowClear
            >
              <Option value={0}>上架</Option>
              <Option value={1}>下架</Option>
            </Select>
          </Form.Item>
          <Form.Item name="detail" label="详情">
            <TextArea rows={4} placeholder="输入产品描述" />
          </Form.Item>
          <Form.Item label="缩略图">
            <Upload
              className="flex justify-center items-center"
              name="file"
              action={process.env.REACT_APP_UPLOAD_SERVER}
              headers={
                localStorage.getItem('__web_token')
                  ? { Authorization: `Bearer ${localStorage.__web_token}` }
                  : null
              }
              showUploadList={false}
              onChange={onChange}
            >
              {imageUrl ? (
                <img
                  src={process.env.REACT_APP_SERVER + imageUrl}
                  alt="avatar"
                  className=" w-72 h-24 bg-red-400"
                />
              ) : (
                <div className="flex justify-center items-center w-24 h-24 bg-red-400">
                  <PlusOutlined />
                </div>
              )}
            </Upload>
          </Form.Item>
          <Form.Item wrapperCol={{ span: 4, offset: 4 }}>
            <Button type="link" htmlType="submit">
              保存
            </Button>
          </Form.Item>
        </Form>
      </Modal>
      <Table dataSource={tableData} rowKey="id">
        <Column title="编号" dataIndex="_id" />
        <Column title="名称" dataIndex="name" />
        <Column
          title="封页图片"
          dataIndex="freeze"
          render={(_, record) => (
            <img src={process.env.REACT_APP_SERVER + record.imageUrl} alt="product" className="w-24 h-24 bg-red-400" />
          )}
        />
        <Column title="单价" dataIndex="price" />
        <Column title="收益" dataIndex="income" />
        <Column title="购买总数" dataIndex="stock" />
        <Column title="限购数量" dataIndex="limit" />
        <Column
          title="操作"
          dataIndex=""
          render={(_, record) => (
            <div>
              <Button type="link" onClick={() => edit(record)}>
                修改
              </Button>
              <Button type="link" onClick={() => del(record)}>
                删除
              </Button>
            </div>
          )}
        />
      </Table>
    </>
  );
};

export default Product;
