import http from './http'


export const LoginApi = (model) => http.post('/admin/login', model)
export const RegisterApi = (model) => http.post('/admin/register', model)
export const CaptchaApi = () => http.get('/common/captcha')

export const DashBoardApi = () => http.get('/admin/info/dashboard')

export const UserListApi = () => http.get('/admin/user/list')
export const UserBanApi = (id) => http.get('/admin/user/' + id)
export const UserEditApi = (model) => http.post('/admin/user', model)
export const UserDelApi = (id) => http.delete('/admin/user/' + id)

export const RostateApi = () => http.get('/admin/info/rotate')
export const RostateSaveApi = (model) => http.post('/admin/info/rotate', model)
export const RostateDelApi = (id) => http.delete('/admin/info/rotate/' + id)

export const PostApi = () => http.get('/admin/info/post')
export const PostSaveApi = (model) => http.post('/admin/info/post', model)
export const PostDelApi = (id) => http.delete('/admin/info/post/' + id)

export const AboutApi = () => http.get('/admin/info/about')
export const AboutSaveApi = (model) => http.post('/admin/info/about', model)

export const ProductApi = () => http.get('/admin/product')
export const ProductSaveApi = (model) => http.post('/admin/product', model)
export const ProductDelApi = (id) => http.delete('/admin/product/' + id)

